* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: F1Regular;
  src: url(https://www.formula1.com/etc/designs/fom-website/fonts/F1Regular/Formula1-Regular.ttf);
}

@font-face {
  font-family: F1Bold;
  src: url(https://www.formula1.com/etc/designs/fom-website/fonts/F1Bold/Formula1-Bold.ttf);
}

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  color: #FFF;
  background-color: #343a40;
}

select.form-control,
textarea.form-control,
input.form-control {
  font-size: 16px;
}
input[type=file] {
  width: 100%;
}
.App {
  margin-top: 20px;
  margin-bottom: 75px;
  background: #343a40;
  padding: 30px;
  font-family: 'Titillium Web', sans-serif;
}

h1 {
  font-size: 24px;
  font-family: F1Bold;
  /* font-family: 'Titillium Web', sans-serif; */
  letter-spacing: 0.2px;
}

h2 {
  color: #6c757d;
  font-size: 18px;
  letter-spacing: 0.2px;
  margin-bottom: 50px;
}

.form-inline .form-control {
  font-family: F1Regular;
  color: #fff;
  border: none;
  border-radius: 4px;
  background: #191b1d;
  box-shadow: none;
  text-align: center;
}

.form-group {
  margin-bottom: 3px;
}

label {
  padding: 0px 10px;
  font-family: 'Titillium Web', sans-serif;
  font-size: 18px;
  font-weight: normal;
}

.btn-default, .btn-default:focus {
  margin: 5px;
  margin-top: 30px;
  color: #fff;
  font-family: F1Regular;
  font-size: 16px;
  border: none;
  border-radius: 0px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
}

#clear {
  background-color: #6c757d;
}

#clear:hover {
  background-color: #FFF;
  color: #6c757d;
}

#calculate {
  background-color: #e10600;
}

#calculate:hover {
  background-color: #FFF;
  color: #e10600;
}

.result {
  border-radius: 4px;
  min-height: 300px;
  font-family: F1Regular;
  background: #191b1d;
  color: #6c757d;
  margin-top: 30px;
  font-size: 16px;
  padding: 30px;
}

.result-title {
  font-size: 30px;
  font-family: 'Titillium Web', sans-serif;
  color: #28a745;
  margin-top: 5px;
  margin-bottom: 5px;
}

.result-title-fail {
  font-size: 30px;
  font-family: 'Titillium Web', sans-serif;
  color: #ffc107;
  margin-top: 78px;
  margin-bottom: 5px;
}

.result-subtitle {
  margin-bottom: 20px;
}

.media {
  padding: 10px;
}

.media-body {
  text-align: center;
}

.green {
  color: #28a745;
}

.footer {
  color: #000;
  margin-top: 10px;
  font-size: 11px;
}

.form-control:focus, .form-control:active, .btn-default:focus, .btn-default:active {
  outline: none !important;
  box-shadow: none !important;
  -webkit-box-shadow: none !important;
}
